.skillContainer{
	margin: 10px;
	display: flex;
	box-shadow: 0px 0px 4px 1px;
	height: 40%;
	padding: 5px;
}

.skillContainer:hover{
	box-shadow: 0px 0px 8px 1px;
}

.iconContainer{
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.skillDescriptionBox{
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 100%;
  }

.skillTitle{
	display: flex;
	align-items: center;
	justify-content: center;
	height: 20%;
	width: 90%;
	padding-top: 5px;
	font-size: 28px;
}

.skillDescription{
	font-size: 22px;
	padding: 20px;
}


.reactIcon{
	display: flex;
	animation: App-logo-spin infinite 20s linear;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 800px){
	.skillContainer{
		padding: 10px;
		height: 20%;
		aspect-ratio: 1/1;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.backButtonContainer{
		height: 10%;
	}

	.skillTitle{
		justify-content: flex-end;
		visibility: hidden;
		position: absolute;
	}
}
