.contentsWrapper{
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	flex-direction: column;
	position: relative;
	-ms-overflow-style: none;
	scrollbar-width: none;
}

.example::-webkit-scrollbar {
    display: none;
}

.linksWrapper{
	position: absolute;
	bottom: 0;
	display: flex;
	width: 100vw;
	align-self: center;
	justify-self: flex-end;
	min-width: 200px;
	margin-bottom: 10px;
	z-index: 1;
}


.typewriterWrapper{
	width: 50%;
	height: 15%;
	display: flex;
	align-self: center;
	justify-content: space-between;
	align-items: center;
}

.contentTypewriter{
	width: 80%;
}

.nextPageButton{
	display: flex;
	align-items: center;
	justify-content: center;
	transform: rotate(-90deg);
	scale: 50%;
	width: 10%;
}


@media screen and (max-width: 800px){
	.contentsWrapper{
		justify-content: space-around;
		height: 100%;
		width: 100%;
	}

	.typewriterWrapper{
		justify-self: flex-start;
		top: 20%;
		width: 90%;
	}

	.linksWrapper{
		position: unset;
		align-items: flex-start;
		align-self: center;
		justify-self: center;
		flex-direction: column;
		gap: 15px;
	}
}
