
.homeContainer{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-size: xx-large;
	opacity: 90%;
}

.contentDisplay{
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100vw;
	height: 100vh;
}

@media screen and (max-width: 800px){
	.homeContainer{
		aspect-ratio: unset;
		height: 100%;
		font-size: x-large;
	}
}


