.projectThumbnailDisplayWrapper{
	display: flex;
	align-items: center;
	justify-content: center;
	width: 80%;
	aspect-ratio: 1/1;
	border: 1px solid black;
	box-shadow: 5px 5px 5px 0px black;
}
.projectName{
	height: 10%;
}

.projectThumbnailDisplayWrapper img{
	width: 100%;
	height: 100%;
	object-position: 100% 50%;
	object-fit: contain;
}

.projectThumbnailDisplayWrapper:hover{
	animation: float 1s infinite alternate;
}
.comingSoon{
	font-size: 20px;
}

@keyframes float{
	from{
		transform: translateY(0px);
		box-shadow: 5px 5px 5px 0px black;
		}
	to{
		transform: translateY(-5px);
		box-shadow: 7px 7px 7px 0px black;
		}
}

.thumbnailWrapper{
	display: flex;
	flex-direction: column;
	gap: 10px;
	height: 100%;
	width: 100%;
}

.projectName{
	color: white;
	font-size: 18px;
	text-align: center;
}

.comingSoon{
	text-align: center;
	color: black;
}

@media screen and (max-width: 800px){
	.thumbnailWrapper{
		align-items: center;
		height: 100%;
		width: 80%;
	}

	.projectThumbnailDisplayWrapper{
		display: flex;
		height: 100%;
		border: 1px solid black;
		box-shadow: 5px 5px 5px 0px black;
	}

	.projectThumbnailDisplayWrapper img{
		object-position: 50% 50%;
	}

	.projectThumbnailDisplayWrapper:hover{
		animation: unset;
	}

	.projectName{
		font-size: 14px;
	}
}
