.projectsWrap{
	display: flex;
	width: 100%;
	height: 100%;
}

.indexContainer{
	height: 100%;
}

.projectsContainer{
	display: flex;
	justify-content: flex-end;
	width: 100%;
}

.projectDisplayContainerMax{
	height: 100%;
	width: 100%;
}
.indexContainer{
	max-width: 15%;
	height: 100vh;
}

.indexContainer::-webkit-scrollbar{
	display: none;
}

.projectDisplayContainerMin{
	width: 80%;
	aspect-ratio: 1/1;
	padding: 5px;
}

.containerContainer{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	height: 90%;
	gap: 10px;
	padding: 5px;
}

.backButtonBox{
	height: 10%;
	margin: 5px;
}


@media screen and (max-width: 800px){
	.projectsWrap{
		flex-direction: column;
	}

	.projectsContainer{
		display: flex;
		height: 100%;
		width: 100%;
	}

	.containerContainer{
		align-items: flex-start;
		justify-content: flex-start;
		flex-direction: row;
		width: 80%;
		gap: 20px;
	}

	.projectDisplayContainerMin{
		gap: 20px;
		aspect-ratio: unset;
	}

	.indexContainer{
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 100%;
		width: 100%;
		height: 10%;
	}

	.backButtonBox{
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.hamburgerBox{
		padding: 10px;

	}

}
